<template>
  <div class="home">
    <h1>Welcome to the Crypto game</h1>
    <p><i>The game that makes you go crypto</i></p>

    <div>
      <div v-if="playerName == ''">
        <input
        placeholder='Insert player name'
        v-model="insertedName"
        @keyup.enter='addPlayerName'/>
        
        <button
        @click='addPlayerName'>
        Submit</button>
          
      </div>
      <div v-else>
        <p v-if='playerName !== ""'>
        Player <b>{{playerName}}</b>
        </p>
      </div>
    </div>

  <div 
  v-if="counter == -1">
  {{this.leaderBoard}}
  </div>
  

    <DateComponent 
    v-bind:dayToday='dayToday'
    v-bind:counter='counter'
    @nextDay='nextDay'
    @submitResult='submitResult'/>
    <br/>
    <br/>
    <StockState v-bind:assets='assets'/>

    <BuySell 
    v-if='this.counter > 0'
    v-bind:assets='assets'
    v-bind:USD='USD'/>

    <MoneyLeft v-bind:USD='USD'/>

    <div>
      <h3>Net worth</h3>
      {{roundUp(this.netWorth)}} $
    </div>

    <NewsComponent 
    v-bind:dayToday='dayToday'/>
  </div>
</template>

<script>
// @ is an alias to /src
// component import
import BuySell from '@/components/BuySell.vue'
import StockState from '@/components/StockState.vue'
import MoneyLeft from '@/components/MoneyLeft.vue'
import DateComponent from '@/components/DateComponent'
import NewsComponent from '@/components/NewsComponent'

// JSON data import
/* const setData = () => {
  const xhr = new XMLHttpRequest ();
  xhr.open ('GET', 'https://gofile.io/?c=CRglTo');
  xhr.setRequestHeader('adminCode', 'KQhy5ULxLEjcdeObabOY');
  xhr.onload = () => {
    console.log (xhr.response);
    return xhr.response
  };
  
  xhr.send ();
}
setData () */

if (typeof leaderBoardJSON == 'undefined') {
  var leaderBoardJSON = '{"player_1": 2000}';
}
console.log (JSON.parse (leaderBoardJSON))
/* console.log (this.leaderBoard) */
var btc = '{"date":{"0":"Apr 21, 2020","1":"Apr 20, 2020","2":"Apr 17, 2020","3":"Apr 16, 2020","4":"Apr 15, 2020","5":"Apr 14, 2020","6":"Apr 13, 2020","7":"Apr 10, 2020","8":"Apr 09, 2020","9":"Apr 08, 2020","10":"Apr 07, 2020","11":"Apr 06, 2020","12":"Apr 03, 2020","13":"Apr 02, 2020","14":"Apr 01, 2020","15":"Mar 31, 2020","16":"Mar 30, 2020","17":"Mar 27, 2020","18":"Mar 26, 2020","19":"Mar 25, 2020","20":"Mar 24, 2020","21":"Mar 23, 2020"},"price":{"0":2.581,"1":2.585,"2":2.559,"3":2.537,"4":2.551,"5":2.564,"6":2.568,"7":2.537,"8":2.523,"9":2.515,"10":2.534,"11":2.618,"12":2.608,"13":2.678,"14":2.652,"15":2.663,"16":2.697,"17":2.674,"18":2.692,"19":2.718,"20":2.714,"21":2.721},"change":{"0":"-0.14","1":"1.02","2":"0.85","3":"-0.55","4":"-0.51","5":"-0.16","6":"1.22","7":"0.55","8":"0.32","9":"-0.75","10":"-3.19","11":"0.36","12":"-2.61","13":"0.98","14":"-0.41","15":"-1.26","16":"0.86","17":"-0.67","18":"-0.96","19":"0.15","20":"-0.26","21":"-1.20"}}';
var gld = '{"date":{"0":"Apr 21, 2020","1":"Apr 20, 2020","2":"Apr 17, 2020","3":"Apr 16, 2020","4":"Apr 15, 2020","5":"Apr 14, 2020","6":"Apr 13, 2020","7":"Apr 10, 2020","8":"Apr 09, 2020","9":"Apr 08, 2020","10":"Apr 07, 2020","11":"Apr 06, 2020","12":"Apr 03, 2020","13":"Apr 02, 2020","14":"Apr 01, 2020","15":"Mar 31, 2020","16":"Mar 30, 2020","17":"Mar 27, 2020","18":"Mar 26, 2020","19":"Mar 25, 2020","20":"Mar 24, 2020","21":"Mar 23, 2020"},"price":{"0":2.581,"1":2.585,"2":2.559,"3":2.537,"4":2.551,"5":2.564,"6":2.568,"7":2.537,"8":2.523,"9":2.515,"10":2.534,"11":2.618,"12":2.608,"13":2.678,"14":2.652,"15":2.663,"16":2.697,"17":2.674,"18":2.692,"19":2.718,"20":2.714,"21":2.721},"change":{"0":"-0.14","1":"1.02","2":"0.85","3":"-0.55","4":"-0.51","5":"-0.16","6":"1.22","7":"0.55","8":"0.32","9":"-0.75","10":"-3.19","11":"0.36","12":"-2.61","13":"0.98","14":"-0.41","15":"-1.26","16":"0.86","17":"-0.67","18":"-0.96","19":"0.15","20":"-0.26","21":"-1.20"}}';
var bitcoinData = JSON.parse (btc);
var goldData = JSON.parse (gld);
var stockData = {
  'Bitcoin': bitcoinData,
  'Gold': goldData
  };
var numberOfDays = Object.keys (bitcoinData.date).length - 1;

export default {
  name: 'Home',
  components: {
    BuySell,
    StockState,
    MoneyLeft,
    DateComponent,
    NewsComponent
  },
  data () {
    var leaderBoard = JSON.parse (leaderBoardJSON);
    return {
      assets: {
        'Bitcoin': {
          name: 'Bitcoin',
          id: 0,
          value: 0,
          price: bitcoinData.price [numberOfDays],
          change: goldData.change [numberOfDays]
        },
        'Gold': {
          name: 'Gold',
          id: 1,
          value: 0,
          price: goldData.price [numberOfDays],
          change: goldData.change [numberOfDays]
        }
      },
      USD: {amount: 5000},
      netWorth: 5000,
      dayToday: {
        today: bitcoinData.date [numberOfDays],
        numberOfDays: numberOfDays,
        news: "You won't believe what happened today. Click <b>here</b> to find out more."
      },
      counter: numberOfDays,
      playerName: '',
      insertedName: '',
      leaderBoard
    }
  },
  mounted () {
    for (var asset in this.assets) {
      if (localStorage [asset]) {
        this.assets [asset] ['value'] = Number (localStorage [asset]);
      }
    }
    if (localStorage.USD) {
      this.USD.amount = Number (localStorage.USD);
    }
    if (localStorage.counter) {
      this.counter = Number (localStorage.counter);
      this.dayToday.today = bitcoinData.date [this.counter];
      for (asset in this.assets) {
        this.assets [asset] ['price'] = stockData [asset] ['price'] [this.counter];
        this.assets [asset] ['change'] = stockData [asset] ['change'] [this.counter];
      }
    }
    if (localStorage.netWorth) {
      this.netWorth = Number (localStorage.netWorth);
    }
    if (localStorage.playerName) {
      this.playerName = localStorage.playerName;
    }
  },
  watch: {
    counter (newCounter) {
      localStorage.counter = newCounter;
    }
  },
  methods: {
    roundUp (number) {
    var parsed = parseFloat (number);
    return parsed.toFixed (2);
    },
    nextDay () {
      if (this.playerName == "") {
        alert ('Insert player name');
      } else {
        this.counter -= 1;
        this.dayToday.today = bitcoinData.date [this.counter];
        this.netWorth = this.USD.amount
        for (var asset in stockData) {
          this.assets [asset] ['price'] = stockData [asset] ['price'] [this.counter];
          this.assets [asset] ['change'] = stockData [asset] ['change'] [this.counter];
          this.assets [asset] ['value'] = this.assets [asset] ['value'] / stockData [asset] ['price'] [this.counter + 1] * stockData [asset] ['price'] [this.counter];
          this.netWorth += this.assets [asset] ['value'];
          localStorage.netWorth = this.netWorth;
          localStorage [asset] = this.assets [asset] ['value'];
        }
      }
    },
    addPlayerName () {
      if (this.insertedName !== "") {
        this.playerName = this.insertedName;
        localStorage.playerName = this.playerName;
      } else {
        alert ('Insert player name.')
      }
    },
    submitResult () {
      this.leaderBoard [this.playerName] = this.netWorth;
      leaderBoardJSON = JSON.stringify (this.leaderBoard);
      this.counter -= 1;
    },
  }
}
</script>